<template>
  <div id="page-login">
    <Icon
      id="page-login-btn-back"
      name="arrow-left"
      @click="jumpToPreviousPage"
    />
    <VanImage
      id="page-login-icon"
      round
      width="144px"
      height="144px"
      fit="cover"
      :src="require('@/assets/others/default_avatar.png')"
      @click="jumpToPageHome"
    />
    <Form
      id="page-login-form"
      ref="pageLoginForm"
      :show-error="false"
      @submit="doSubmitFormValues($event, urls.getRSAPubKey, urls.postLoginVal)"
    >
      <Field
        ref="loginFieldUserName"
        v-model="formValues.userName"
        name="userName"
        label="用户名"
        placeholder="请输入6至20个字符（字母或数字）"
        :rules="rulesUserName"
      />
      <Field
        ref="loginFieldPassword"
        v-model="formValues.password"
        type="password"
        name="password"
        label="密码"
        placeholder="请输入8至20个字符（字母或数字）"
        :rules="rulesPassword"
      />
      <div id="page-login-btns">
        <Button
          id="page-login-btn-submit"
          round
          block
          type="info"
          native-type="submit"
        >
          登录
        </Button>
        <Button
          id="page-login-btn-register"
          round
          block
          type="default"
          native-type="button"
          @click="jumpToPageRegistration"
        >
          没有账号？点我注册
        </Button>
      </div>
    </Form>
  </div>
</template>

<style scoped>
#page-login {
  overflow: auto;
}

#page-login-btn-back {
  position: absolute;
  top: 16px;
  left: 16px;
  width: 40px;
  font-size: 16px;
  line-height: 40px;
  text-align: center;
  color: white;
  background-color: var(--primary-color);
  border-radius: 50%;
}

#page-login-icon {
  display: block;
  margin: 64px auto;
}

#page-login-btns {
  padding: 32px 16px;
}

#page-login-btn-register {
  margin-top: 16px;
}
</style>

<script>
import { SHA256Calculator } from '@/utils/hashCalculator.js'
import { axiosRenewAccessToken, axiosEncryptWithRSAOAEP } from '@/utils/axiosInterceptor.js'
import { urlAPIGetRSACryptionKey, urlAPIPostUserLogin } from '@/api/requestURLs.js'
import { Button, Field, Form, Icon, Image as VanImage, Notify } from 'vant'

export default {
  name: 'PageLogin',
  components: {
    Button,
    Field,
    Form,
    Icon,
    VanImage
  },
  data () {
    return {
      urls: {
        getRSAPubKey: urlAPIGetRSACryptionKey,
        postLoginVal: urlAPIPostUserLogin
      },
      routePath: {
        pathFrom: '',
        pathTo: ''
      },
      formValues: {
        userName: '',
        password: ''
      },
      rulesUserName: [
        {
          required: true,
          message: '请填写用户名'
        },
        {
          message: '',
          validator: this.checkUserNameFormat
        }
      ],
      rulesPassword: [
        {
          required: true,
          message: '请填写密码'
        },
        {
          message: '',
          validator: this.checkPasswordFormat
        }
      ]
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.routePath = {
        pathFrom: from.path,
        pathTo: to.path
      }
    })
  },
  beforeRouteLeave (to, from, next) {
    this.$refs.pageLoginForm.resetValidation()
    next()
  },
  methods: {
    // 检查用户名格式是否正确
    checkUserNameFormat (value, rule) {
      const pattern = /^[a-z0-9]+$/i
      let checkPassed = false
      if (!pattern.test(value)) {
        rule.message = '不应包含除字母和数字以外的字符'
      } else if (value.length < 6) {
        rule.message = '字符不应少于6个'
      } else if (value.length > 20) {
        rule.message = '字符不应多于20个'
      } else {
        checkPassed = true
      }
      return checkPassed
    },
    // 检查密码格式是否正确
    checkPasswordFormat (value, rule) {
      const pattern = /^[a-z0-9]+$/i
      let checkPassed = false
      if (!pattern.test(value)) {
        rule.message = '不应包含除字母和数字以外的字符'
      } else if (value.length < 8) {
        rule.message = '字符不应少于8个'
      } else if (value.length > 20) {
        rule.message = '字符不应多于20个'
      } else {
        checkPassed = true
      }
      return checkPassed
    },
    // 显示服务器连接失败通知
    notifyBadConnection () {
      Notify({
        type: 'danger',
        message: '连接服务器失败'
      })
    },
    // 从后端获取RSA-OAEP公钥
    getRSAPubKey (url) {
      return axiosRenewAccessToken().get(url)
        .then(response => response.data.payload.key)
        .catch(error => Promise.reject(error))
    },
    // 前端向后端发送加密后的信息
    postRSAEncryptedMsg (url, rsaPubKey, message) {
      return axiosEncryptWithRSAOAEP(rsaPubKey).post(url, message)
        .then(response => response)
        .catch(error => {
          const resMsg = error?.response?.data?.message
          if (resMsg?.includes('key: \'RequestUserLogin.userName\'')) {
            this.$refs.loginFieldUserName.validateMessage = '用户名格式不正确'
          }
          if (resMsg?.includes('key: \'RequestUserLogin.password\'')) {
            this.$refs.loginFieldPassword.validateMessage = '密码格式不正确'
          }
          if (resMsg === 'user does not exist') {
            this.$refs.loginFieldUserName.validateMessage = '用户名不存在'
          }
          if (resMsg === 'crypto/bcrypt: hashedPassword is not the hash of the given password') {
            this.$refs.loginFieldPassword.validateMessage = '密码不正确'
          }
          return Promise.reject(error)
        })
    },
    // 异步提交表单数据
    async submitFormValues (values, urlGetRSAPubKey, urlPostLoginVal) {
      // 从后端获取RSA-OAEP公钥
      const rsaPubKey = await this.getRSAPubKey(urlGetRSAPubKey)
      // 加密相关表单数据
      values.password = SHA256Calculator(values.password)
      // 前端向后端发送加密后的信息
      const response = await this.postRSAEncryptedMsg(urlPostLoginVal, rsaPubKey, values)
      // 返回响应结果
      return response
    },
    // 执行异步提交表单数据
    doSubmitFormValues (values, urlGetRSAPubKey, urlPostLoginVal) {
      // 未成功连接服务器前不执行后续操作
      if (!this.$store.getters.isConnected) {
        this.notifyBadConnection()
        return
      }
      // 在连接服务器的前提下，提交表单数据
      return this.submitFormValues(values, urlGetRSAPubKey, urlPostLoginVal)
        .then(response => {
          // 登录成功则保存JWT
          this.$store.dispatch('updateAppJWTString', response.headers.authorization)
          // 登录成功的通知
          Notify({
            type: 'success',
            message: '登录成功'
          })
          // 跳转到来源页面
          this.$router.replace(this.routePath.pathFrom)
        })
        .catch(() => {
          // 连接服务器失败
          if (!this.$store.getters.isConnected) {
            this.notifyBadConnection()
            return
          }
          // 登陆失败
          Notify({
            type: 'danger',
            message: '登录失败'
          })
        })
    },
    // 跳转到前一页面
    jumpToPreviousPage () {
      this.$router.back()
    },
    // 跳转到首页
    jumpToPageHome () {
      this.$router.push('/')
    },
    // 跳转到注册页面
    jumpToPageRegistration () {
      this.$router.replace('/registration')
    },
    // 跳转到来源页面
    jumpToPageFrom () {
      if (this.routePath.pathFrom !== this.routePath.pathTo) {
        this.$router.replace(this.routePath.pathFrom)
      } else {
        this.jumpToPageHome()
      }
    }
  }
}
</script>
