import CryptoJS from 'crypto-js'

// 使用SHA256的散列值计算器
function SHA256Calculator (message) {
  if (typeof message !== 'string') {
    message = JSON.stringify(message)
  }
  const hashDigest = CryptoJS.SHA256(message)
  const hashDigestBase64 = CryptoJS.enc.Base64.stringify(hashDigest)
  return hashDigestBase64
}

export {
  SHA256Calculator
}
